/* eslint-disable */
import axios from "axios";

const state = { 
    hospitalId:0,
    typeBed:0,
    hospitalDetail:[],
    loadingDetailHospital:'',
};

const getters = { 
    hospitalDetail: state => state.hospitalDetail,
    loadingStateDetailHospital: state => state.loadingDetailHospital,
};

const actions = { 
    async getDetail({commit}){
        commit('setLoadingDetailHospital', true)
        await axios.get(`get-bed-detail?hospitalid=${state.hospitalId}&type=${state.typeBed}`)
       .then((res)=>{
        commit('setLoadingDetailHospital', false)
        commit('setDetail',res.data.data )
       })
     },
};

const mutations = { 
    setDetail: (state, hospitalData) => (
        state.hospitalDetail = hospitalData
    ),
    setHospitalId:(state, value) => {
        state.hospitalId = value
    },
    setTypeBed:(state,value) => {
        state.typeBed = value
    },
    setLoadingDetailHospital(state ,data) {
        state.loadingDetailHospital = data
    },
};

export default {
    state,
    getters,
    actions,
    mutations
}