import Vue from "vue";
import VueRouter from "vue-router";
import Container from "../views/Container.vue";
// import NotFound from "../views/NotFound.vue";

import JumbotronHospitalBed from "../components/Jumbotron/JumbotronHospitalBed";
import Hospital from "../components/Hospital/Hospital";

import JumbotronTracker from "../components/Jumbotron/JumbotronTracker";

Vue.use(VueRouter);

const routes = [
  {
    path: "*",
    redirect: { name: "Container" },
  },

  {
    path: "/",
    redirect: "/JumbotronHospitalBed",
    component: Container,
    name: "Container",
    children: [
      {
        path: "/",
        name: "JumbotronHospitalBed",
        component: JumbotronHospitalBed,
        meta: {
          title: "JumbotronHospitalBed",
        },
      },
      {
        path: "/hospitals",
        name: "Hospital",
        component: Hospital,
        meta: {
          title: "Hospital",
        },
      },
      {
        path: "/tracker",
        name: "JumbotronTracker",
        component: JumbotronTracker,
        meta: {
          title: "JumbotronTracker",
        },
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
});

export default router;
