<template>
  <div class="container col-xxl-8 px-4 py-5">
    <div class="row flex-lg align-items-center g-5 py-5">
      <h1 class="display-5 mt-5 fw-bold lh-1 text-center">
        Ketersediaan Tempat Tidur Rumah Sakit.
      </h1>
      <div class="col-10 col-sm-8 col-lg-6">
        <img
          src="@/assets/images/medstaff.png"
          class="d-block mx-lg-auto img-fluid"
          alt="Bootstrap Themes"
          width="700"
          height="500"
          loading="lazy"
        />
      </div>
      <div class="col-md-10 mx-auto col-lg-5">
        <form class="p-4 p-md-5 border rounded-3 bg-light">
          <div class="form-floating mb-3">
            <h6 class="text-muted">Pilih Tempat Tidur</h6>
            <div
              class="btn-group w-100"
              role="group"
              aria-label="Basic radio toggle button group"
            >
              <input
                type="radio"
                class="btn-check"
                value="1"
                v-model="sTypeBed"
                id="radioCovid"
                checked
              />
              <label class="btn btn-outline-secondary w-auto" for="radioCovid"
                >Covid 19</label
              >

              <input
                type="radio"
                class="btn-check"
                value="2"
                v-model="sTypeBed"
                id="radioNonCovid"
              />
              <label
                class="btn btn-outline-secondary w-auto"
                for="radioNonCovid"
                >Non Covid 19</label
              >
            </div>
          </div>
          <div class="form-floating mb-3">
            <h6 class="text-muted">Pilih Provinsi</h6>
            <v-select
              :options="provincesList"
              v-model="sProv"
              :reduce="(name) => name.id"
              label="name"
              :disabled="loadingState === true"
              placeholder="Pilih Provinsi"
              :clearable="false"
              :loading="loadingState === true ? true : false"
            >
              <div
                v-if="loadingState === true"
                style="border-left-color: rgba(88, 151, 251, 0.71)"
                class="vs__spinner"
              ></div>
            </v-select>
          </div>
          <div class="form-floating mb-3">
            <h6 class="text-muted">Pilih Kabupaten / Kota</h6>
            <v-select
              :options="cityList"
              v-model="sCity"
              :reduce="(name) => name.id"
              label="name"
              :disabled="loadingState === true"
              placeholder="Pilih Kota"
              :clearable="false"
              :loading="loadingState === true ? true : false"
            >
              <div
                v-if="loadingState === true"
                style="border-left-color: rgba(88, 151, 251, 0.71)"
                class="vs__spinner"
              ></div
            ></v-select>
          </div>

          <button
            @click="getHospitals"
            class="w-100 btn btn-lg btn-primary"
            type="button"
            :disabled="loadingState === true"
          >
            Cari
            <!-- <span v-if="loadingState === true">
              <i class="fa fa-circle-o-notch fa-spin"></i>
            </span> -->
          </button>
        </form>
      </div>
    </div>
  </div>

  <!-- <div class="container col-xl-10 col-xxl-8 px-4 py-5">
    <div class="row align-items-center g-lg-5 py-5">
      <div class="col-lg-7 text-center text-lg-start">
        <h1 class="display-4 fw-bold lh-1 mb-3">Cari Rumah Sakit</h1>
        <p class="col-lg-10 fs-4">
          <img
            src="@/assets/images/medstaff.png"
            class="d-block mx-lg-auto img-fluid"
            alt="Bootstrap Themes"
            width="700"
            height="500"
            loading="lazy"
          />
        </p>
      </div>
      
    </div>
  </div> -->
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  data() {
    return {
      sTypeBed: 1,
      sProv: "",
      sCity: 0,
    };
  },

  computed: {
    ...mapGetters(["provincesList"]),
    ...mapGetters(["cityList"]),
    ...mapGetters(["loadingState"]),
  },
  mounted() {
    this.sProv = "11prop";
    this.sCity = "";
  },
  watch: {
    sTypeBed() {
      this.$store.commit("setSelectedTypeBed", this.sTypeBed);
    },
    sProv() {
      this.$store.commit("setLoading", true);
      this.$store.commit("setSelectedProvinces", this.sProv);
      //buat dapetin array kota pertama
      this.getCities().then(() => {
        this.$store.commit("setLoading", false);
        this.sCity = String(this.$store.state.getHospitals.firstCity);
      });
    },
    sCity() {
      this.$store.commit("setSelectedCities", Number(this.sCity));
    },
  },
  //   updated() {
  //     console.log(this.$store.state.ProvCity);
  //   },
  created() {
    this.getProvince();
  },
  methods: {
    getHospitals() {
      this.$router.push({
        path: "/hospitals",
        query: {
          province: this.sProv,
          cityid: this.sCity,
          type: this.sTypeBed,
        },
      });
    },
    ...mapActions(["getProvince", "getCities"]),
  },
};
</script>
