/* eslint-disable */
import axios from "axios";

const state = { 
    provinces: [],
    cities:[],
    hospitals:{
        hospitals:[],
        maps:[],
    },
    selectedProv:'11prop',
    selectedCity:0,
    selectedTypeBed:0,
    firstCity:0,
    loading:''
};

const getters = { 
    provincesList: state => state.provinces,
    cityList: state => state.cities,
    hospitalList: state => state.hospitals.hospitals,
    mapsList: state => state.hospitals.maps,
    loadingState: state => state.loading,
};

const actions = { 
    async getProvince({commit},selectValue){
       await axios.get("get-provinces")
      .then((res)=>{
        selectValue = '11prop'
        commit("setProvinces", res.data.provinces)
        commit("setSelectedProvinces", selectValue)
      })
    },

    async getCities({commit},selectValue){
        await axios.get(`get-cities?provinceid=${state.selectedProv}`)
        .then((res)=>{
            selectValue = String(1101)
            commit("setCities", res.data.cities)
            commit("setFirstCity", Number(res.data.cities[0].id))
            commit("setSelectedCities", selectValue)
        }); 
      },

      async getHospitals({commit,dispatch}){
        
        commit('setLoading', true)
        await axios.get(`get-hospitals?provinceid=${state.selectedProv}&cityid=${state.selectedCity}&type=${state.selectedTypeBed}`).then((res)=>{
            commit('setLoading', false)
            commit("setHospitals", res.data.hospitals)
        }).then(()=>{
            let array = state.hospitals.hospitals
  
            let promises = []

            const getMapsAsync = async() => {
                for (let i = 0; i < array.length; i++) {
                    const element = array[i];
                    promises.push(axios.get(`get-hospital-map?hospitalid=${element.id}`)
                    .then((res)=>{
                        return res.data.data
                    }))
                }

                await Promise.all(promises).then(data => {
                    commit("setMaps", data)
                })
            }

            getMapsAsync();
            
        })
      },
      
    //   async getMaps({commit},hospId){
    //     await axios.get(`get-hospital-map?hospitalid=${hospId}`).then((res)=>{
    //         commit("setMaps", res.data.data)
    //     })
    //   }
};

const mutations = { 
    setProvinces: (state, provinces) => (
        state.provinces = provinces
    ),
    setCities: (state, cities) => (
        state.cities = cities
    ),
    setHospitals: (state, hospitals) => (
        state.hospitals.hospitals = hospitals
    ),
    setMaps:(state,maps)=>{
        state.hospitals.maps = maps
    },
    setSelectedProvinces(state ,data) {
        state.selectedProv = data
    },
    setSelectedCities(state ,data) {
        state.selectedCity = data
    },
    setSelectedTypeBed(state,data){
        state.selectedTypeBed = data
    },
    setFirstCity(state ,data) {
        state.firstCity = data
    },
    setLoading(state ,data) {
        state.loading = data
    },

};

export default {
    state,
    getters,
    actions,
    mutations
}